.root {
  --size: 110vh;
  --s: calc(var(--size) / 6);
  --bor: calc(var(--size) / 30);
  --boxShadow: calc(var(--size) / 12);
}

.body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.body-buyPage1 {
  min-height: 100vh;
  display: flex;
  justify-content: center;
