.footer-distributed {
    background: #122B31;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
    box-sizing: border-box;
    width: 100%;
    text-align: left;
    font: bold 16px sans-serif;
    padding: 55px 50px;

}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right {
    display: inline-block;
    vertical-align: top;
}

.footer-center-content {
    display: flex;
    justify-content: space-between;
}

/* Footer left */

.footer-distributed .footer-left {
    width: 30%;
}

/* The company logo */

.footer-distributed h3 {
    color: #ffffff;
    margin: 0;
}

.footer-distributed h3 span {
    color: lightseagreen;
}

/* Footer links */

.footer-distributed .footer-links {
    color: #ffffff;
    margin: 20px 0 12px;
    padding: 0;
}

.footer-distributed .footer-links a {
    display: inline-block;
    line-height: 1.8;
    font-weight: 400;
    text-decoration: none;
    color: inherit;
}



/* Footer Center */

.footer-distributed .footer-center {
    width: 45%;

}

.footer-distributed .footer-center .footer-center-content>p {
    width: 40%;
}

.footer-distributed .footer-center .footer-center-content>p>span:nth-child(1) {
    font-family: 'Exo 2';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    /* identical to box height, or 29px */

    letter-spacing: -0.2px;

    color: #F8C45F;


    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;
    margin-bottom: 28px;
}

.footer-distributed .footer-center .footer-center-content>p>span:nth-child(2) {
    font-family: 'Exo 2';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    letter-spacing: -0.2px;
    color: #B2A282;
    flex: none;
    order: 1;
    flex-grow: 0;
}

.footer-distributed .footer-center p {
    display: inline-block;
    color: #ffffff;
    font-weight: 400;
    vertical-align: middle;
    margin: 0;
}

.footer-distributed .footer-center p span {
    display: block;
    font-weight: normal;
    font-size: 14px;
    line-height: 2;
}

.footer-distributed .footer-center p a {
    color: lightseagreen;
    text-decoration: none;
    ;
}

.footer-distributed .footer-links a:before {
    content: "|";
    font-weight: 300;
    font-size: 20px;
    left: 0;
    color: #fff;
    display: inline-block;
    padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
    content: none;
}

/* Footer Right */

.footer-distributed .footer-right {
    width: 15%;
    float: right;
}

.footer-distributed .footer-company-about {
    margin: 0;

    font-family: 'Exo 2';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.2px;
    color: #F8C45F;
}

.footer-distributed .footer-company-about span {
    display: block;
    font-family: 'Exo 2';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.2px;
    color: #F8C45F;
    margin-bottom: 20px;
}

.footer-distributed .footer-icons {
    margin-top: 25px;
}

.footer-distributed .footer-icons a {
    display: inline-block;
    width: 35px;
    height: 35px;
    cursor: pointer;
    border-radius: 2px;

    font-size: 20px;
    color: #ffffff;
    text-align: center;
    line-height: 35px;

    margin-right: 3px;
    margin-bottom: 5px;
}

.footer-bottom {
    margin-top: 20px;
    width: 100%;
    color: #B2A282;
    font-size: 14px;
    font-weight: bold;
    padding: 10px 0;
    text-align: center;
}
.footer-company-name {
    color: #B2A282;
    font-size: 14px;
    font-weight: normal;
    margin: 0;
}

@media (max-width: 880px) {

    .footer-distributed {
        font: bold 14px sans-serif;
    }

    
    .footer-distributed .footer-center,
    .footer-distributed .footer-right {
        width: 100%;
        margin-bottom: 40px;
        text-align: center;
    }
    .footer-distributed {
        padding: 55px 20px;
    }
    .footer-distributed .footer-left {
        display: flex;
        justify-content: center;
        width: 100%;
        margin-bottom: 40px;
        text-align: center;
    }
    .footer-distributed .footer-center i {
        margin-left: 0;
    }

}