.title .line1 {
    font-family: 'Frostbite Boss Fight';
    font-style: normal;
    font-weight: 400;
    font-size: 31.3962px;
    line-height: 120%;
    color: #FFFFFF;
}

.title .line2 {
    font-family: 'Frostbite Boss Fight';
    font-style: normal;
    font-weight: 400;
    font-size: 88.52px;
    line-height: 100%;
    color: #17E3E0;
    text-shadow: 0px 10.0112px 10.5381px rgba(23, 227, 224, 0.3);
}
.vlg1 {
    position: absolute;
    z-index: -100;
    top: -8%;
    left: -5%;
}

.homepage {
    width: 100%;
    height: 100vh;
    font-size: 24px;
    position: relative;
}

.homepage .container {
    width: 650px;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.homepage .btn-pay {
    padding: 7px 83px;
}

.total-sur {
    display: flex;
    bottom: 10px;
    transform: translateX(13%);
}

.total-sur>div {
    padding: 24px 34px;
}

.total-sur>div:nth-child(1),
.total-sur>div:nth-child(2) {
    border-right: 1px solid #ebebeb;
}

.total-sur>div>div:nth-child(1) {
    color: #fff;
    font-style: normal;
    font-weight: 600;
    font-size: 12.8498px;
}

.vl {
    position: relative;
    height: 190px;
    width: 100%;
}

.vlim,
.total-sur {
    position: absolute;
}

.total-sur>div>div:nth-child(2) {
    font-style: normal;
    font-weight: 700;
    font-size: 24.9857px;
    line-height: 23px;
    background: linear-gradient(0deg, #FF6D0D 0%, #FFAA00 51.3%, #FFF34F 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.valuebg {
    width: 260px;
    height: 50px;
    background-image: url("../../assets/adressbg.png");
    background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
    background-size: contain;
}

.valuetitle {
    font-style: normal;
    font-weight: 800;
    font-size: 14px;
    line-height: 20px;
    color: #FFFFFF;
    margin-bottom: 12px;
}

.value {
    font-family: 'Exo 2';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    background: linear-gradient(0deg, #FF6D0D 0%, #FFAA00 51.3%, #FFF34F 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    height: 100%;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: center;
    position: relative;
}

.value:after {
    content: url("../../assets/copyic.svg");
    position: absolute;
    right: 15px;
}

.adress {
    margin-top: 70px;
    display: flex;
    /* justify-content: space-between; neu co 2 slot */
    justify-content: center;
}

@media (max-width: 880px) {
    .total-sur {
        padding: 0 5px !important;
    }

    .vlim {
        width: 100%;
        height: 100px;
        bottom: 5px;
    }
    .homepage .container {
        width: 100%;
    }
    .total-sur>div {padding: 2px 15px}
    .total-sur>div>div:nth-child(2) {font-size: 11px;}
    .total-sur>div>div:nth-child(2) {font-size: 14px;}
}
.homepage.cover {
    background-image: url("../../assets/homebg1.png") !important;
}