/* all style start with .bottom-nav-container */

.bottom-nav-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 80px;
    width: 100%;
    min-width: 320px;
    background-color: #1b1e25;
    font-family: 'Helvetica';
   ;
    padding-top: 15px;
    border-top-left-radius: 30px 20px;
    border-top-right-radius: 30px 20px;
}
.bottom-nav-container .nav-item {
    max-width: 10%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: center;
    font-size: 15px;
    font-weight: 400;
    color: #a0a0a0;
    padding: 0 10px;
}
.bottom-nav-container .nav-item:not(.active) {
    font-weight: 400;
    color: #a0a0a0;
}
.nav-child-item {
    font-size: 15px;
    font-weight: 400;
    color: #a0a0a0;
}
.blacksmith-hidden {
    background-color: #1b1e25;
    padding: 10px 10px 25px 10px;
    border-radius: 15px;
}
.bottom-nav-container .nav-item.selected .nav-item-label {
    color: #fff;
    font-weight: 700;
}
.bottom-nav-container .nav-item.active {
    color: #63c5a0;
    font-weight: 700;
}
.bottom-nav-container .nav-item.selected .nav-item-label::before {
    display: block;
    content: attr(title);
    color: white;
    font-weight: 700;
    height: 0;
    overflow: hidden;
    visibility: hidden;
}
.nav-more-btn-container {
    position: relative;
    top: -14px;
    width: 95px;
    height: 115px;
    background-color: #1b1e25;
    border-radius: 80px;
}
.nav-more-btn-container.more {
    margin-left: 14px;
}
.nav-more-btn-container .nav-indicator {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-color: #1b1e25;
    position: absolute;
    top: -5px;
    z-index: -1;
}
.nav-more-btn-container span.more-label {
    position: relative;
}
.nav-item-more-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 3px;
    background: linear-gradient(266.44deg, #aacf91 0.8%, #3bbfa8 100%);
    border-radius: 15px;
    border: none;
    min-width: 70px;
    min-height: 70px;
    border-radius: 50%;
    cursor: pointer;
}

.bottomNav {
    z-index: 9999;
    position: fixed;
    bottom: -2px;
    width: 100%;
}

.blacksmith-hidden {
    display: none;
}