.modepage {
    width: 80%;
    margin: 40px auto;
}

.modepage .panel1 img {
    width: 100%;
}

.modepage .cardi {
    width: 200px;
    height: 100px;
    background-color: #fff;
}

.modepage .panel2 {
    width: 100%;
    margin-top: 40px;
}

.mode-parent {
    position: relative;
}

.mode-parent>div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.mode-parent>div>div:nth-child(1) {
    font-family: 'Frostbite Boss Fight';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 120%;
    color: #FFFFFF;
}

.mode-parent>div>div:nth-child(2) {
    font-family: 'Exo 2';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #FFFFFF;
}
.card-content {
    padding: 25px 20px
}