@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@400;500;600;700;800;900&display=swap');
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face {
  font-family: "Frostbite Boss Fight";
  src: url("./fonts/Frostbite_Boss_Fight.otf")
}

@font-face {
  font-family: "Jersey M54";
  src: local("Russian"),
    url("./fonts/Ambit-Bold.ttf") format("truetype");
  font-weight: normal;
}

