.ant-menu>.ant-menu-item:hover,
.ant-menu-light .ant-menu-submenu-title:hover,
.ant-menu>.ant-menu-submenu:hover,
.ant-menu>.ant-menu-item-active,
.ant-menu>.ant-menu-submenu-active,
.ant-menu>.ant-menu-item-open,
.ant-menu>.ant-menu-submenu-open,
.ant-menu>.ant-menu-item-selected,
.ant-menu>.ant-menu-submenu-selected,
.sidebar .ant-menu-item a:hover,
.sidebar .ant-menu-item a:focus,
.sidebar .ant-menu-item a:active,
.ant-menu-horizontal>.ant-menu-item a:hover,
.ant-menu-item.ant-menu-item-active .ant-menu-item-icon,
.ant-menu-item::selection,
.sidebar .ant-menu-item-active {
    color: #18F2EF !important;

}
.money-header {
    display: flex;
flex-direction: row;
align-items: center;
padding: 12px 24px 12px 108px;
gap: 24px;

width: 100%;
height: 64px;

background: rgba(0, 0, 0, 0.67);

/* Inside auto layout */

flex: none;
order: 1;
flex-grow: 0;
}
.logo,
.ant-menu {
    float: left;
    margin: 0px 24px 16px 0;
}

.connect-wl {
    font-weight: 800;
    margin: 0px 24px 16px 0;
    display: flex;
    gap: 24px;
    justify-content: right;
    height: 88px;
}
.connect-btn {
    background: linear-gradient(180deg, #18F2EF 0%, #065352 45.72%, #004B4A 73.81%, #006B69 100%);
    border: 1px solid #F0A427;

}
.myreward-btn {
    background: linear-gradient(0deg, #FF6D0D 0%, #FFAA00 51.3%, #FFF34F 100%);
    border: 1px solid #7A522E;
    
}
.connect-btn, .myreward-btn {
    color: #fff;
    align-self: center;
    height: 56px;
    justify-content: center;
    font-size: 17.6501px;
    display: flex;
    align-items: center;
    cursor: pointer;
    border-radius: 49px;
    padding: 0 30px;
}

.ant-menu-horizontal>.ant-menu-item-selected a {
    color: #18F2EF !important;
}

.ant-menu-horizontal {
    border: none !important;
}

.ant-layout-header,
.ant-menu {
    height: 88px !important;
    background-color: #001A26 !important;
    line-height: 88px !important;
}

.ant-menu-horizontal>.ant-menu-item::after {
    content: none !important;
}

.ant-menu-item a,
.ant-menu-submenu {
    font-family: "Exo 2", sans-serif;
    font-weight: 800;
}


.ant-menu-horizontal>.ant-menu-item:not(.ant-menu-item-selected) a,
.ant-menu-item:not(.ant-menu-item-selected) .ant-menu-item-icon,
.ant-menu-submenu {
    color: #756E6E;
}

/* add show more icon for submenu after  */
.ant-menu-submenu-title:after {
    content: "\f0d7";
    font-family: "Font Awesome 5 Free";
    font-size: 12px;
    color: #756E6E;
    margin-left: 8px;
}
.ant-menu-submenu-title:hover:after,
.ant-menu-submenu-title:active:after,
.ant-menu-submenu.ant-menu-submenu-selected .ant-menu-submenu-title:after {
    color: #18F2EF;
    animation: all 2s ease;
}
.ant-menu-submenu .ant-menu-item a {
    color: #fff;
}
.ant-menu-submenu .ant-menu-item a:hover {
    color: #18F2EF;
}

.ant-menu-submenu-popup, .ant-menu-sub {
    height: 200px !important;
}
.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
    background: none !important;
}

@media (max-width: 880px) { 
    .ant-layout-header.header, .money-header {
        display: none;
    }
}