.connect-form {
    width: 520px;
    padding-bottom: 0;
    text-align: left;
    vertical-align: middle;
    box-sizing: border-box;
    color: rgba(0,0,0,.85);
    font-size: 14px;
    font-variant: tabular-nums;
    line-height: 1.5715;
    list-style: none;
    font-feature-settings: "tnum","tnum";
    pointer-events: none;
    position: relative;
    max-width: calc(100vw - 32px);
    margin: 0 auto;
    padding: 0 0 24px;
}
.connect-form-content {
    position: relative;
    background: linear-gradient(294.26deg,#1f232a -.68%,#17191e)!important;
    border: 0.5px solid hsla(0,0%,100%,.2)!important;
    border-radius: 12px!important;
    box-shadow: 0 3px 6px -4px rgb(0 0 0 / 12%), 0 6px 16px 0 rgb(0 0 0 / 8%), 0 9px 28px 8px rgb(0 0 0 / 5%);
    pointer-events: auto;
}
.connect-form-body {
    padding: 24px;
    font-size: 14px;
    line-height: 1.5715;
    word-wrap: break-word;
}
#walletRow .walletCol {
    box-shadow: inset 0 4px 68px hsl(0deg 0% 100% / 5%), inset 0 2px 4px hsl(0deg 0% 100% / 10%);
    -webkit-backdrop-filter: blur(68px);
    backdrop-filter: blur(68px);
    grid-row-gap: 6px!important;
    row-gap: 6px!important;
    color: #fff;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    cursor: pointer;
    width: 220px;
    height: 167px;
}
#walletRow .walletCol {
    border-color: hsla(0,0%,100%,.05);
    background: hsla(0,0%,100%,.05);
    border-radius: 20px;
}
#walletRow h4 {
    color: #fff;
    font-size: 16px;
    margin: 0;
}
.modal_header p {
    font-family: "Helvetica";
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    color: #fff;
    margin: 0!important;
}
#walletRow p {
    color: #848484;
    font-size: 12px;
    margin-bottom: 0!important;
}